import { mapState, mapMutations } from 'vuex';
export default {
	computed: {
		...mapState(['hasLogin', 'userId'])
	},
		data() {
			return {
				user:[],
			};
		},
		
		onLoad() {
			let that = this;
			console.log(that.userId);
			
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/select/user/by/id',
				method: 'POST',
				data: {
					id: that.userId
				},
				success: res => {
					console.log(res.data.data)
					that.user = res.data.data;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		onShow() {
			
		},
		methods: {
			changeFace(){
				uni.navigateTo({
				    url: './userInformation/face'
				});
			},
			changeNickName(){
				uni.navigateTo({
				    url: './userInformation/nickName'
				});
			},
			accountSecurity(){
				uni.navigateTo({
				    url: 'userInformation/accountSecurity'
				});
			},
			paymentPassword(){
				uni.navigateTo({
				    url: './userInformation/paymentPassword'
				});
			},
			showType(tbIndex){
				this.tabbarIndex = tbIndex;
				this.list = this.orderList[tbIndex];
			}
		},
		onUnload() {
			uni.setStorage({
				imgUrl:'',
				nickName: '',
				success: function (res) {
					this.userName= res.data.userName;
					this.password= res.data.password;
				    console.log(res.data);
				}
			});
			
		}
	}
